export default {
	data() {
		return {
			ListFlag:false,
			listArr:[],
			time:false
		}
	},
	onLoad(options) {
		this.getListArr()
	},
	onShow(){
		
	},
	methods: {
		yiwancheng:function(item,i){
			
			let that=this;
				let usurl0=that.url1+"/dinghai/order/updateWorkover";
			uni.showModal({
			        title: '此单是否已经完成？',
			        content: "订单ID为："+item.id,
			        cancelText:'是',
			        confirmText:'否',
			        success(res){
			          if(res.cancel){
						  uni.request({
						  		url: usurl0,
						  		header:{
						  			"Content-Type" : "application/x-www-form-urlencoded"
						  		},
						  		method:'POST',
						  		data: {
						  			id:item.id
						  		},
						      success: (res) => {
						  		if(res.statusCode==200){
						  			that.listArr.splice(i,1)
						  			uni.navigateTo({
						  				url:"../daifukuan/daifukuan"
						  			})
						  		}else{
						  			uni.showToast({
						  				title:"您的网络打了个盹",
						  				icon:"none"
						  			})
						  		}
						      },
						  	fail : function(err){
						  		uni.showToast({
						  			title:"您的网络打了个盹",
						  			icon:"none"
						  		})
						  	}
						  });
			          }else if(res.confirm){
			             console.log("点击了取消");
			          }
			        }
			   })
			
			
				
		},
		getListArr:function(){
			let that=this;
				let usurl=that.url1+"/dinghai/order/selectOrder";
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					if(res.statusCode==200){
						that.listArr=res.data.flag
						console.log(that.listArr)
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		
		   //  点击整体时显示功能
		showgn : function(){
			
		}
	}
}
